.WeatherComponent{
    width: 80%;
    height: 90%;
    background-color: rgb(25, 161, 230);
    position:absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 15px;    
    box-shadow: 1px 1px 100px 10px rgba(0, 0, 0, 0.3);
}
.Navbar{
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    background-color: rgb(25, 161, 230);
    padding: 25px 0;
}
.NavItem{
    cursor: pointer;
    padding: 10px 20px;
    color: rgb(217, 229, 235)
}
.NavItem:hover{
    color: white;
}
.item1{
    margin-left: 20px;
}

 @media only screen and (max-width: 420px){
    .Navbar{
        display: none;
    }
}

@media only screen and (max-width: 420px){
    .WeatherComponent{
        width: 100%;
        height: 100%;
        /* background: linear-gradient(-360deg, rgb(25, 161, 230), rgb(9, 72, 104)); */
        position:absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 0;    
        box-shadow: none;
        border-radius: 0;  
    }
} 