.Search{
    position: fixed;
    right: 100px;
    margin: 10px 0;
}
.Search input{
    padding: 8px 10px;
    padding-left: 20px;
    border: 3px solid white;
    border-right: 3px solid rgb(255, 255, 255); 
    border-radius: 50px; 
    outline: none;
    position: absolute;
    right: 2px;
    width: 250px;
}
.Search input:focus{
    color: rgb(0, 0, 0);
}
.Search button{
    border-radius: 50px;
    border: 3px solid white;
    position: absolute;
    top: 2px;
    right: 5px;
    z-index: 4444444;
    color: rgb(0, 0, 0);
    padding: 6px 10px;
    background-color: rgb(119, 195, 233);
}
.Search button:hover{
    background-color: rgb(63, 156, 202);
} 
.fa-search{
    color: white;
}  

@media only screen and (max-width: 420px){
    .Search{
        top: 20px;
        right: 60px;
        border-radius: 10px;
        width: 70%;
    }
    .Search input{
        padding: 15px 10px !important; 
        border-radius: 50px !important;
        border: 3px solid white;
        width: 100%;
        left: 0px;
        padding-left: 20px !important;
    }
    .Search button{
        padding: 15px 10px !important; 
        border-radius: 50px;
        border: 3px solid white;
        position: absolute;
        right: 16px;
        z-index: 4444444;
        color: rgb(0, 0, 0);
        background-color: transparent;
        top: 0px;
    }
    .Search button:hover{
        background-color: transparent;    
    } 
    .fa-search{
        color: black !important;
    }  
}  
