.WeatherImage{
    height: 250px;
    animation: leftright 1.2s linear infinite alternate-reverse;
    /* filter: invert(1); */
}
@keyframes leftright {
    0% {
      transform: translateX(-0.3rem);
    }
    100%{
      transform: translateX(0.1rem);
    }
  }
@media only screen and (max-width: 420px){
    .WeatherImage{
        height: 180px;
        /* filter: invert(1); */
    }
} 