*{
    box-sizing: border-box;
}
.WeatherData{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    height: auto;
    /* border: 1px solid black; */
    margin: auto !important;
    margin-bottom: 50px !important;
}
.WeatherData1{
    border-right: 1px solid rgb(121, 183, 213)
}
.WeatherData1 h1{
    margin-top: 20px!important;
}
.WeatherData1 .fa-street-view{
    font-size: 40px;
    padding-right: 10px;
}

.WeatherData1 h1{
    font-size: 50px;
}
.WeatherData2 h1{  
    font-size: 100px;
    font-weight: bold;
    color: white;
    text-align: center !important;
}
.minmax{
    margin: 10px 0;
    padding: 5px;
    color: white;
}
.minmaxpara{
    padding: 4px 10px;
    font-size: 30px;
}
.para1{
    border-right: 1px solid  rgb(200, 217, 225); 
}
.weatherDesc{
    font-size: 30px;
    color: white;
}
.wdata{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-width: 50%;
    /* border: 1px solid white; */
}


.DataDetails{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    height: 66px;
    /* border: 1px solid black; */
    margin: auto !important;
    position: absolute;
    bottom: 0;
    color: white;
}
.DataDetails1{
    border-right: 1px solid rgb(121, 183, 213);
}
.DataDetails1 p{
    margin: 0;
}
.DataDetails2 p{
    margin: 0;
}
.data{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    font-size: 20px;
    /* border: 1px solid white; */
}
.data p{
    padding: 0 10px;
    font-weight: 600;
}
.data i{
    font-size: 25px;
}
.hr{
    color: white;
}

 @media only screen and (max-width: 420px){
    .WeatherData1{
        border: none;
    }
    .WeatherData1 h1{
        font-size: 40px;
        margin: 0;
    }
    .wdata{
        min-width: 100%;
    }
    .WeatherData{
        margin: 120px 0 !important;
        margin-bottom: 83px !important;
    }
    .minmax{
        margin: 0px;
        padding: 5px;
        color: white;
    }
    .minmaxpara{
        padding: 4px 10px;
        font-size: 30px;
    }
    .para1{
        border-right: 1px solid  rgb(121, 183, 213); 
    }
    .weatherDesc{
        padding-bottom: 20px !important;
        font-size: 25px;
        color: white;
    }
    .hr{
        color: white;
    }
    .hrhidden{
        display: none;
    }

    .DataDetails{
        height: 90px;
        bottom: 10px;
    }
    .DataDetails1{
        border-right: 0px;
        padding-right: 40px !important;
    }
    .DataDetails2{
        padding-left: 40px !important;
    }
    .data{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 50%;
        font-size: 20px;
        /* border: 1px solid white; */
    }
    .data p{
        padding: 0 10px;
        font-weight: 600;
    }
    .data i{
        font-size: 25px;
    }
}  