.error{
    position: absolute;
    background-color: rgb(255, 255, 255);
    color: rgb(0, 0, 0) !important;
    top: 0;
    left: 50%;
    z-index: 56566;
    padding: 10px 50px;
    transform: translateX(-50%);
    border-radius: 6px;
}
@media only screen and (max-width: 420px){
    .error{
        padding: 0;
        border-radius: 0;
        width: 100%;
    }
} 