.autodetectbtn{
    color: rgb(255, 255, 255);
    border: none;
    width: 50px;
    height: 50px;
    background-color: rgb(22, 133, 189);
    position: absolute;
    top: 8px;
    z-index: 3333333;
    right: 10px;
    font-size: 20px;
    transform: translateX(-50%);
    border-radius: 50%;
    
}
.autodetectbtn:hover{
    background-color: rgb(113, 194, 235);
}
.fa-rotate-right{
    transition: all 0.8s;
}
.fa-rotate-right:hover{
    transform: rotate(360deg);
}

@media only screen and (max-width: 420px){
    .autodetectbtn{
        width: 60px;
        height: 60px;
        padding: 8px 13px;
        background-color: rgb(255, 255, 255);
        position: absolute;
        top: 645px;
        z-index: 3333333;
        left: 50%;
        font-size: 20px;
        transform: translateX(-50%);
        border-radius: 50%;
        box-shadow: 0px 0px 25px 10px rgba(0, 0, 0, 0.2);
    }
    .fa-rotate-right{
        color: black;
    }
} 